.mainBox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
}

.lobbyContainer{
  width:100vw;
  background-color: black;
  margin: 0;
  padding: 0;
  display:         -ms-flexbox;
  display:         flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
}

.componentContainer {
  width: 65vw;
  background: rgb(31, 30, 30);
  border-radius: 10px;
}

@media (max-width: 768px) {
  .componentContainer {

  }
}

/* Preferences */

.preferenceContainer {
  color: white;
  width: 60vw;

  margin: 0 auto;
  padding-top: 25px;
}

.prefBarLabel, .trendingLabel {
  font-size: 24px;
}

.prefBarLabel {
  margin-bottom: 10px;
 }

.trendingLabel {
  margin-top: 25px;
  margin-bottom: 10px;
}


/* Animation */

.homeAni-appear {
   opacity: 0.01;
}

.homeAni-appear.homeAni-appear-active {
   opacity: 1;
   -webkit-transition: opacity 300ms ease-in;
   -o-transition: opacity 300ms ease-in;
   transition: opacity 300ms ease-in;
}



